.Maintenance__button-container {
  padding: 0;
  text-align: center;
  position: relative;
}

.Maintenance__close-order {
  position: absolute;
  left: 0;
  top: 0;
  color: var(--blue);
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;

  width: 100%;
  height: 100%;
  -webkit-transition: all 200ms linear;
  -o-transition: all 200ms linear;
  transition: all 200ms linear;
  outline: none;
}

.Maintenance__button-container:hover .Maintenance__close-order {
  background: var(--red);
  color: var(--white);
}



