:root {
  --landing-bg-color: #1d1d1d;
  --left-bg-color: rgba(177, 182, 199, 0.7);
  --left-button-hover-color: rgba(74, 115, 254, 0.3);
  --right-bg-color: rgba(74, 115, 254, 0.3);
  --right-button-hover-color: rgba(177, 182, 199, 0.7);
  --speed: 700ms;
}
.Landing {
  padding: 0;
  margin: 0;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  position: relative;
  background: var(--landing-bg-color);
}

.Landing__header {
  font-size: 4rem;
  font-family: var(--font-second);
  text-transform: uppercase;
  letter-spacing: 5px;
  color: #f3f3f3;
  text-shadow: 0 0 16px rgba(0, 0, 0, 0.5);
}

.landing-button {
  height: 2.5rem;
  width: 200px;
  text-align: center;
  background: var(--blue);
  color: var(--white);
  border: var(--blue) solid 3px;
  font-size: 1rem;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  -webkit-transition: all 100ms linear;
  -o-transition: all 100ms linear;
  transition: all 100ms linear;
  border-radius: 9999px;
  outline: none;
}

.split {
  position: absolute;
  width: 50vw;
  height: 100%;
  overflow: hidden;
}

.landing-button:hover {
  color: var(--blue);
  background: var(--white);
  border: var(--white) 3px solid;
}

.Landing_left-owner {
  left: 0;
  background: url('https://images.unsplash.com/photo-1448630360428-65456885c650?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=844d3a349e587ef4188df4ddc79c2a70&auto=format&fit=crop&w=1947&q=80')
    center center no-repeat;
  background-size: cover;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: end;
      align-items: flex-end;
  padding-right: 50px;

  -webkit-transition: var(--speed) all ease-in-out;

  -o-transition: var(--speed) all ease-in-out;

  transition: var(--speed) all ease-in-out;
}

.Landing_left-owner.split:before {
  position: absolute;
  width: 100%;
  height: 100%;
  background: var(--left-bg-color);
  -webkit-transition: var(--speed) all ease-in-out;
  -o-transition: var(--speed) all ease-in-out;
  transition: var(--speed) all ease-in-out;
}

.Landing_right-resident {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: start;
      align-items: flex-start;
  padding-left: 50px;

  right: 0;
  background: url('https://images.unsplash.com/photo-1460317442991-0ec209397118?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=b95599cb95166892018645cd2a22923a&auto=format&fit=crop&w=1950&q=80')
    center center no-repeat;
  background-size: cover;
  -webkit-transition: var(--speed) all ease-in-out;
  -o-transition: var(--speed) all ease-in-out;
  transition: var(--speed) all ease-in-out;
}

.Landing_right-resident.split:before {
  position: absolute;
  width: 100%;
  height: 100%;
  background: var(--right-bg-color);
  -webkit-transition: var(--speed) all ease-in-out;
  -o-transition: var(--speed) all ease-in-out;
  transition: var(--speed) all ease-in-out;
}

.Landing_link {
  color: var(--white);
  text-decoration: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 0.8rem;
  letter-spacing: 2px;
}



