.OwnerRegistration {
  display: grid;
  grid-template-areas: 'sidebar content';
  grid-template-rows: 1fr;
  grid-template-columns: 200px 1fr;
  height: 100vh;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: stretch;
      align-content: stretch;
  background-color: var(--white);
}

.OwnerRegistration__logo-container {
  grid-area: sidebar;
  height: 100%;
  background-color: var(--blue);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.OwnerRegistration__logo {
  width: 70%;
  -o-object-fit: cover;
     object-fit: cover;
}

.OwnerRegistration__logo-header {
  font-weight: normal;
  color: var(--white);
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 0.75rem;
  position: absolute;
  bottom: 50px;
}

.OwnerRegistration__login-container {
  grid-area: content;
}

.OwnerRegistration__header {
  margin-bottom: 5px;
}
.OwnerRegistration__subheader {
  margin-bottom: 30px;
}

.OwnerRegistration__input {
  background: var(--white);
  border: 1px solid var(--gray);
  width: 300px;
  border-radius: 9999px;
  padding: 8px 15px;
  font-size: 14px;
  margin: 5px 0;
  outline: none;
}

.OwnerRegistration__input.OwnerRegistration__input--email:focus,
.OwnerRegistration__input.OwnerRegistration__input--password:focus {
  border: 1px solid var(--blue);
}

.OwnerRegistration__submit {
  background: var(--blue);
  color: var(--white);
  border: var(--blue) solid 3px;
  font-size: 0.75rem;
  font-weight: bold;
  text-transform: uppercase;
  border-radius: 100px;
  text-align: center;
  letter-spacing: 2px;
  width: 300px;
  height: 35px;
  padding: 0 60px;
  margin: 5px 0 20px;
  outline: none;
}

.OwnerRegistration__footer {
  font-size: 0.75rem;
}

.OwnerRegistration__disclaimer {
  font-size: 0.5rem;
  margin-bottom: 10px;
}

.OwnerRegistration__link {
  font-size: 0.75rem;
  color: var(--blue);
}

.OwnerRegistration__link:hover {
  color: var(--dark-gray);
}



